import React, { useEffect, useState } from 'react'
import { firestore } from '../firebase/firebase.utils'

export default function Test({ user }) {
  const [publish, setPublish] = useState(null)
  const [draft, setDraft] = useState(null)
  useEffect(() => {
    let unpublished = []
    firestore
      .collection('questions')
      .get()
      .then((querySnapshot) => {
        querySnapshot.docs.forEach((doc) => {
          const questions = []
          firestore
            .collection('questions')
            .doc(doc.id)
            .collection('DRAFT')
            .get()
            .then((ques) => {
              ques.forEach((question) => {
                questions.push({
                  id: question.id,
                  ...question.data(),
                })
              })
            })
          unpublished.push({
            category: doc.id,
            questions,
          })
        })
      })
      .then(() => {
        setDraft(unpublished)
      })
  }, [])

  useEffect(() => {
    let published = []
    firestore
      .collection('questions')
      .get()
      .then((querySnapshot) => {
        querySnapshot.docs.forEach((doc) => {
          const questions = []
          firestore
            .collection('questions')
            .doc(doc.id)
            .collection('PUBLISHED')
            .get()
            .then((ques) => {
              ques.forEach((question) => {
                questions.push({
                  id: question.id,
                  ...question.data(),
                })
              })
            })
          published.push({
            category: doc.id,
            questions,
          })
        })
      })
      .then(() => {
        setPublish(published)
      })
  }, [])

  useEffect(() => {
    console.log('publish')
    console.log(publish)
  }, [publish])

  useEffect(() => {
    console.log('draft')
    console.log(draft)
  }, [draft])

  return (
    <div>
      <h1>Hello World</h1>
    </div>
  )
}
