import React, { useState, useEffect } from 'react'
// import { Tabs, Tab, TabList, TabPanel } from 'react-tabs'
import { Tabs, Tab } from 'react-bootstrap'
import 'react-tabs/style/react-tabs.css'

import Question from './Question'

export default function CategoryQuestions({
  questions,
  user,
  type,
  publishUpdate,
}) {
  const [key, setKey] = useState('')
  const [data, setData] = useState(null)

  useEffect(() => {
    setData(questions)
  }, [questions])

  return (
    <>
      {data ? (
        <Tabs
          id="controlled-tab-example"
          activeKey={key}
          onSelect={(k) => setKey(k)}
        >
          {data.map((category, index) => (
            <Tab
              eventKey={category.category}
              title={category.category}
              key={index}
            >
              <Question
                publishUpdate={publishUpdate}
                questionData={category.questions}
                quiz={category.category}
                user={user}
                type={type}
              />
            </Tab>
          ))}
        </Tabs>
      ) : // <Tabs defaultFocus={true}>
      //   <TabList defaultChecked={true}>
      //     {data.map((cat, index) => {
      //       return <Tab key={index}>{cat.category}</Tab>
      //     })}
      //   </TabList>
      //   {data.map((cat, index) => (
      //     <TabPanel defaultChecked={true} key={index}>
      //       <Question
      //         questionData={cat.questions}
      //         quiz={cat.category}
      //         user={user}
      //         type={type}
      //       />
      //     </TabPanel>
      //   ))}
      // </Tabs>
      // <Tabs id='controlled-tab-example' onSelect={k => setKey(k)}>
      //   {data.map((cat, index) => {
      //     return (
      //       <Tab eventKey={cat.category} title={cat.category} key={index}>
      //         <Question
      //           questionData={cat.questions}
      //           quiz={cat.category}
      //           user={user}
      //         />
      //       </Tab>
      //     )
      //   })}
      // </Tabs>

      null}
    </>
  )
}
