import React, { useState } from 'react'
import { Form, Button, Alert } from 'react-bootstrap'
import { Redirect } from 'react-router-dom'
import { loginWithFirestore } from '../firebase/firebase.utils'

import 'bootstrap/dist/css/bootstrap.min.css'

export default function Login({ user, setUser }) {
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [role, setRole] = useState('')
  const [error, setError] = useState(false)

  const login = async (e) => {
    e.preventDefault()
    setError(false)
    if (email && password && role) {
      const response = await loginWithFirestore(role, email, password)

      if (response?.status === 'SUCCESS') {
        const { data } = response
        localStorage.setItem('someQuizzySecret', JSON.stringify(data))
        setUser(data)
      } else {
        setError(true)
      }
    }
  }

  return (
    <div className="login">
      {user ? (
        <>
          {user.role === 'admin' ? (
            <Redirect to="/verify" />
          ) : (
            <Redirect to="/add" />
          )}
        </>
      ) : (
        <div>
          <h1>Quizzy</h1>
          <Form onSubmit={login}>
            <Form.Group>
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                type="text"
                placeholder="email"
                name="email"
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </Form.Group>
            <Form.Group>
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="password"
                name="password"
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </Form.Group>
            <Form.Text>
              Please enter email and password provided by Quizzy Team
            </Form.Text>
            <Button
              variant="primary"
              type="submit"
              style={{ margin: '20px 0' }}
              onClick={() => setRole('que-user')}
            >
              I wan't to draft quiz questions
            </Button>
            <br />
            <Button
              variant="dark"
              type="submit"
              onClick={() => setRole('admin')}
              style={{ margin: '20px 0' }}
            >
              I wan't to publish quiz questions
            </Button>
          </Form>

          {error ? (
            <Alert variant="danger">Invalid credential or Role</Alert>
          ) : null}
        </div>
      )}
    </div>
  )
}
