import React, { useEffect, useState } from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import { checkForAuth } from './firebase/firebase.utils'

import Login from './components/Login'
import './App.css'
import Add from './components/Add'
import Welcome from './components/Welcome'
import Navbar from './components/Navbar'
import VerifyWrapper from './components/VerifyWrapper'
import EditQuestion from './components/EditQuestion'
import NotFound from './components/NotFound'
import Test from './components/Test'

export const API_URL = 'https://us-central1-quizzy-a4b01.cloudfunctions.net/'

function App () {
  const [user, setUser] = useState(null)
  const [link, setLink] = useState(null)

  useEffect(() => {
    const data = JSON.parse(localStorage.getItem('someQuizzySecret'))
    if (data) {
      checkAuth(data)
    }
    async function checkAuth () {
      const response = await checkForAuth(data.role, data.id)
      if (response?.status === 'SUCCESS') {
        setUser(response.data)
        if (response.data.role === 'admin') {
          setLink('/verify')
        } else if (response.data.role === 'que-user') {
          setLink('/add')
        }
      }
    }
  }, [])

  return (
    <Router>
      <div className='App'>
        <Navbar user={user} setUser={setUser} setLink={setLink} />
        <Route path='/' exact component={() => <Welcome link={link} />} />
        <Route
          path='/login'
          exact
          render={() => <Login user={user} setUser={setUser} />}
        />
        <Route path='/add' exact render={() => user && <Add user={user} />} />
        <Route
          path='/verify'
          exact
          render={() => <VerifyWrapper user={user} />}
        />
        <Route
          path='/edit/:id'
          render={({ match }) => {
            return match ? (
              <EditQuestion match={match} user={user} />
            ) : (
              <NotFound />
            )
          }}
        />
        <Route
          path='/test'
          render={() => {
            return user && <Test user={user} />
          }}
        />
      </div>
    </Router>
  )
}

export default App
